@use "../global/mixins.scss";
@use "../global/variables.scss";

.social__list {
  @include mixins.list-reset;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0 10px;
  width: 220px;
}

.social__link {
  @include mixins.link-reset;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 9px;

  &:hover .social__icon {
    color: variables.$shamrock;
  }

  &:active .social__icon {
    color: rgba(variables.$shamrock, 0.3);
  }
}

.social__icon {
  color: variables.$black-brown;
}
